import * as React from "react";
import { Tag } from 'antd-mobile'
import FilterBox from "./FilterBox"
import { getCardList } from "../../api/card"


import time from '../../assets/img/pay/time.png'
import money from '../../assets/img/pay/money.png'
import active from '../../assets/img/active.png'
interface ISprops {
    [propName: string]: any,

}
interface IState {
    cardList: any[],
    [propName: string]: any,
}

export default class FilterComponent extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            cardList: []
        }
    }

    componentWillMount() {
        this.initCard()
    }
    initCard() {
        getCardList({}).then(res => {
            if (!res.status) {
                let list = res.data;
                this.setState({
                    cardList: list || []
                })
            }
        })
    }

    // 重置城市筛选
    resetCard = () => {
        const { cardList } = this.state
        cardList.map((el,index) => {
            el.selected = false;
            cardList[index] = {...el};
        })
        this.setState({
            cardList: []
        }, () => {
            this.setState({
                cardList
            })
        })
    }

    // 更新城市筛选
    updateCard = () => {
        let card: any[] = [];
        let { cardList } = this.state
        cardList.map((el) => {
            if(el.selected) {
                card.push(el.erp_card_id)
            }
        })
        this.props.onChange(card.toString())
    }

    render() {
        const { cardList } = this.state;
        return (
            <FilterBox
                onReset={this.resetCard}
                onChange={this.updateCard}
                visible={this.props.visible}
            >
                <div className="filter-card">
                    {cardList.map((el, index) => (

                        <Tag
                            selected={el.selected}
                            onChange={(selected) => { el.selected = selected }}
                            key={index}>   
                            <div className="card-item">
                                <img className="active" src={active} alt=""/>
                                <img className="card" src={el.cost_type === 1 ? money : time } alt="" />
                                <div className="title">上海高顿教育有限公司</div>
                                <div className="con">
                                {el.cost_type === 1 ? 
                                    <React.Fragment>
                                        总金额 ￥<span>{el.total_amount}</span> 
                                    </React.Fragment>
                                    
                                    : 
                                    <React.Fragment>
                                        总次数 <span>{el.total_number}次</span> 
                                    </React.Fragment>
                                }</div>
                                <div className="number">{el.card_no}</div>
                                <div className="type">{el.cost_type === 1 ? '储值卡' : '财税卡'}</div>
                            </div>
                        </Tag>
                    ))}
                </div>
            </FilterBox>

        )
    }

}
