import * as React from "react";
import moment from 'moment'
import { Flex, Calendar, Badge } from 'antd-mobile'
import CityFilter from "./CityFilter"
import SeriesFilter from "./SeriesFilter"
import CardFilter from './CardFilter'

import search from '../../assets/img/top/search.png'

interface ISprops {
    [propName: string]: any,

}
interface IState {
    config: any,
    calendarShow: boolean,
    cityList: any[],
    citysShow: boolean,
    seriesList: any[],
    seriesShow: boolean,
    [propName: string]: any,
    cardShow: boolean
}

export default class FilterComponent extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            config: {},
            calendarShow: false,
            cityList: [],
            citysShow: false,
            seriesList: [],
            seriesShow: false,
            filter: {
                citys: '',
                start_time: null,
                end_time: null,
                series_id: '',
                erp_card_ids:''
            },
            cardShow:false
        }
    }
    componentWillMount() {
        const {filters} = this.props;
        const {filter} = this.state;
        this.setState({
            filter: {...filter, ...filters}
        })
    }
    updateFilter = (newFilter) => {
        let {filter} = this.state
        filter = {...filter, ...newFilter};
        this.setState({
            filter
        })

        this.handleFilterHide()
        this.props.onChange(filter)
    }
    toggleFilterShow = (keyName: string) => {
        let toggleTag = this.state[keyName];
        let showsKeyList = ['calendarShow', 'citysShow', 'seriesShow', 'cardShow'];
        let state = {};
        showsKeyList.map((show) => {
            state[show] = (keyName === show ? !toggleTag : false)
        })
        this.setState(state)
    }
    handleFilterHide = () => {
        this.toggleFilterShow('')
    }
    // 更新开课时间筛选
    updateCalendar = (startDateTime, endDateTime) => {
        this.updateFilter({
            start_time: moment(startDateTime).format("YYYY-MM-DD"),
            end_time: moment(endDateTime).format("YYYY-MM-DD"),
        })
    }
    handleClearTime = () => {
        //清除事件操作
        this.updateFilter({
            start_time: null,
            end_time: null,
        })
    }
    // 更新城市筛选
    updateCity = (citys) => {
        this.updateFilter({
            citys: citys
        })
    }
    // 更新系列选择
    updateSeries = (series_id) => {
        this.updateFilter({
            series_id
        })
    }
    // 更新卡
    updateCard = (card) => {
        this.updateFilter({
            erp_card_ids: card
        })
    }
    
    render() {
        const { calendarShow, citysShow, seriesShow, filter, cardShow} = this.state;
        const { start_time, end_time, citys, series_id, erp_card_ids} = filter;
        const { vipFlag, consumerFlag } = this.props;
        let cityArr = citys.split(',') || [];
        let seriesArr = series_id.split(',') || [];
        let erpArr = erp_card_ids.split(',') || [];
        return (
            <div className="filter-c">
                <Flex className="filter-header">
                    <Flex.Item onClick={() => this.toggleFilterShow('calendarShow')}>{this.props.calendarTit || ''} <img src={search} alt=""/>
                    {
                        start_time ? 
                            <Badge text="2"></Badge>
                            :
                            <div></div>
                    }
                    </Flex.Item>
                    {
                        consumerFlag ?
                        <Flex.Item onClick={() => this.toggleFilterShow('cardShow')}>{this.props.cardTit || ''} <img src={search} alt=""/>
                            {
                                !erp_card_ids ?
                                    <div></div>
                                    :
                                    <Badge text={erpArr.length}></Badge>
                            }
                        </Flex.Item>
                        :
                        <Flex.Item onClick={() => this.toggleFilterShow('citysShow')}>{this.props.citysTit || ''} <img src={search} alt=""/>
                            {
                                !citys  ?
                                    <div></div>
                                    :
                                    <Badge text={cityArr.length}></Badge>
                            }
                        </Flex.Item>
                    }
                    
                    {
                        vipFlag ?
                            <div></div>
                            :
                            <Flex.Item onClick={() => this.toggleFilterShow('seriesShow')}>{this.props.seriesTit} <img src={search} alt=""/>
                                    {
                                        !series_id ?
                                            <div></div>
                                            :
                                            <Badge text={seriesArr.length}></Badge>
                                    }
                            </Flex.Item>
                    }
                </Flex>

                <CardFilter 
                    visible={cardShow}
                    card={erp_card_ids}
                    onChange={this.updateCard}/>

                {/* 日历选择 */}
                <Calendar
                    rowSize="xl"
                    defaultValue={start_time ? [new Date(start_time), new Date(end_time)] : undefined}
                    visible={calendarShow}
                    onCancel={this.handleFilterHide}
                    onConfirm={this.updateCalendar}
                    onClear={this.handleClearTime}
                />

                {/* 城市选择 */}
                <CityFilter 
                    visible={citysShow}
                    city={citys}
                    onChange={this.updateCity}/>

                {/* 系列选择 */}
                <SeriesFilter
                    visible={seriesShow}
                    series={series_id}
                    onChange={this.updateSeries}/>
            </div>

        )
    }

}
