import * as React from "react";
import { Tag } from 'antd-mobile'
import FilterBox from "./FilterBox"
import {getCourseSeries} from "../../api/index"

interface ISprops {
    [propName: string]: any,

}
interface IState {
    seriesList: any[],
    [propName: string]: any,
}

export default class FilterComponent extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            seriesList: [],
        }
    }
    componentWillMount() {
        this.initSeries()
    }
    initSeries() {
        let {series} = this.props;
        let seriesArray = series.split(",");
        getCourseSeries().then(res => {
            let list = res.data;
            list.map((el) => {
                if(seriesArray.includes(el.id.toString())) {
                    el.selected = true;
                }
            })
            this.setState({
                seriesList: list
            })
        })
    }

    // 重置系列选择
    resetSeries = () => {
        const {seriesList} = this.state
        seriesList.map((el, index) => {
            el.selected = false;
            seriesList[index] = {...el};
        })
        this.setState({
            seriesList: []
        }, () => {
            this.setState({
                seriesList
            }) 
        })
    }

    // 更新系列选择
    updateSeries = () => {
        let series: any[] = [];
        let {seriesList} = this.state
        seriesList.map((el) => {
            if(el.selected) {
                series.push(el.id)
            }
        })
        this.props.onChange(series.toString())
    }
    
    render() {
        const {seriesList} = this.state;
        return (
                <FilterBox
                    onReset={this.resetSeries}
                    onChange={this.updateSeries}
                    visible={this.props.visible}
                >
                    <div className="filter-series">
                        {seriesList.map((el, index) => (
                            <Tag style={{fontSize:'.13rem',}}
                                selected={el.selected}
                                onChange={(selected) => el.selected = selected}
                                key={index}
                            >{el.name}</Tag>
                        ))}
                    </div>
                </FilterBox>

        )
    }

}
