import * as React from "react";
import { Flex, Button } from 'antd-mobile'
interface ISprops {
    [propName: string]: any,
}
interface IState {
}

class FilterBox extends React.Component<ISprops, IState>{
    render() {
        return (
            <div className="filter-box" style={{display: this.props.visible ? 'block' : 'none'}}>
                <div className="filter-mask"></div>
                <div className="filter-content">
                    <div className="filter-content-bd">
                        {this.props.children}
                    </div>
                    {
                        this.props.letterArr ?
                            <div className="city-right">
                                {
                                    this.props.letterArr.map( (el,index) => (
                                        <a key={index} href={`#${el}`}>{el}</a>
                                    ))
                                }
                            </div>
                            :
                            <div></div>
                    }
                    
                    <div className="filter-content-ft">
                        <Flex className="filter-content-ft">
                            <Flex.Item>
                                <Button onClick={this.props.onReset}>重置</Button>
                            </Flex.Item>
                            <Flex.Item>
                                <Button onClick={this.props.onChange} type="primary">确定</Button>
                            </Flex.Item>
                        </Flex>
                    </div>
                </div>
            </div>
        )
    }

}

export default FilterBox 