//react 基础组件加载
import * as React from "react";
import { NoticeBar, ListView, Toast } from 'antd-mobile';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../actions';

import FootAdvertising from '../components/advertising/footAdvertising';
import Footer from '../components/footer';
import AllForm from '../components/search/allForm';
import FilterComponent from '../components/search/filterComponent';
import CourseItem from '../components/courseItem'
import CourseEmpty from '../components/course/CourseEmpty'
import DownLoad from '../components/down/downLoad'
import { getCourseList } from '../api/course';
import { getSeriesTime } from '../api/index'

import download from '../assets/img/download.png'
import '../assets/less/courselist.less';


const mapStateToProps = (state: any) => ({
        store: state
})

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}

interface ISprops {
    [propName: string]: any,
}
interface IState {
    dataSource: any,
    isLoading: Boolean,
    hasMore: Boolean,
    pageFrom: boolean,
    listPad: boolean,
    footType: number,
    query: any,
    filter: any,
    downShow: boolean
}


class CourseList extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
        this.state = {
            pageFrom: true,
            listPad: false,
            footType: 2,//footerbar
            query: '',
            filter: {
                citys: '',
                start_time: '',
                end_time: '',
                series_id: ''
            },

            dataSource,
            isLoading: true,
            hasMore: true,
            downShow: false
        }
    }
    courseList: Object[] = [];
    page_num: number = 1;
    page_size: number = 10;

    componentWillMount() {
        document.title = '面授课程';
        let series_id = this.props.location.query || '' ;
        let {filter} = this.state; 
        let { search } = this.props.match.params;
        if(series_id !== '' ) {//传入navbar参数
            filter.series_id = series_id.toString();
        } 
        this.setState({
            query: search,
            filter
        }, () => {
            this.queryListData()
        })
    }
    delayTime() {
        let self = this;
        setTimeout(function () {
            self.setState({
                listPad: true
            })
        }, 3000)
    }
    componentDidMount() {
        this.delayTime()
    }
    // 筛选项修改
    handleFilterChange = (filters) => {
        this.setState({
            filter: { ...filters }
        }, () => {
            this.page_num = 1;
            this.queryListData()
        })
    }
    queryListData() {
        Toast.loading('');
        this.setState({ isLoading: true });
        let { page_num, page_size } = this;
        let { filter, query } = this.state;
        getCourseList({
            page_num,
            page_size,
            course_name: query,
            ...filter
        }).then(res => {
            Toast.hide();
            if (!res.status) {
                let { list, total_pages, page_num } = res.data;
                // 新获取的数据直接拼接
                if (page_num === 1) {
                    this.courseList = [];
                }
                this.courseList = [...this.courseList, ...list];

                // 如果还有下一页，页码加1
                if (page_num < total_pages) {
                    this.page_num += 1
                }

                this.setState({
                    dataSource: this.state.dataSource.cloneWithRows(this.courseList),
                    isLoading: false,
                    hasMore: page_num < total_pages
                })
                if(filter.series_id !== '') {
                    getSeriesTime({
                        seriesId: filter.series_id
                    }).then(res => {
                        console.log(res)
                    })
                }
            }
        })
    }
    // 滚动加载
    onEndReached = (event) => {
        if (this.state.isLoading || !this.state.hasMore) {
            return;
        }
        this.queryListData()
    }

    downLoad = () => {
        //下载大纲
        this.setState({
            downShow: true
        })
    }
    closeDownLoad = () => {
        this.setState({
            downShow: false
        })
    }

    render() {
        const renderFooter = () => (
            this.state.hasMore
                ?
                <div style={{ textAlign: 'center', lineHeight: '.26rem' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>
                :
                <div></div>
        )
        const Row = (rowData, sectionID, rowID) => (
            <CourseItem key={rowID} course={rowData} />
        )
        const { listPad, footType, query, filter, downShow, isLoading } = this.state;
        const { courseList } = this;
        return (
            <div className={listPad ? "course-list" : "course-list pad"}>
                <div className="search-top">
                    {
                        listPad || <NoticeBar mode="closable">课程表仅供参考，最终课程信息请以课前通知为准！</NoticeBar>
                    }
                    <AllForm title="请输入课程名称" searchValues={query} searchForm={0}/>
                    <FilterComponent 
                        calendarTit='开课时间'
                        citysTit='开课城市'
                        seriesTit='专业系列'
                        filters={filter} 
                        onChange={this.handleFilterChange} 
                    />
                </div>
                {
                    courseList.length === 0 && !isLoading?
                        <CourseEmpty source={1}/>
                        :
                        // <CourseEmpty source={1}/>
                        <React.Fragment>
                            <ListView
                            dataSource={this.state.dataSource}
                            renderFooter={courseList.length === 0 ? () => (<div></div>) : renderFooter}
                            renderRow={Row}
                            className="index-list"
                            pageSize={4}
                            useBodyScroll
                            scrollRenderAheadDistance={500}
                            onEndReached={this.onEndReached}
                            onEndReachedThreshold={10}
                        />
                        <div className="down-pic" onClick={this.downLoad}>
                            <img src={download} alt=""/>
                        </div>
                        </React.Fragment>
                }
                <DownLoad title="课表" downShow={downShow} filter={filter} query={query} source={true} closeDownLoad={this.closeDownLoad} downLoad={this.downLoad}/>
                <FootAdvertising title="course"/>
                <Footer footType={footType} />
            </div>
        );
    }

}
export default connect(mapStateToProps, mapDispatchToProps)(CourseList);