import * as React from "react";
import {Tag} from 'antd-mobile'
import FilterBox from "./FilterBox"
import {getCourseCity } from "../../api/index"


interface ISprops {
    [propName: string]: any,

}
interface IState {
    cityList: any,
    [propName: string]: any,
}

export default class FilterComponent extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            cityList: null,
            letterArr: []
        }
    }
    
    componentWillMount() {
        this.initCity()
        this.getLetter()
    }
    getLetter = () => {
        let arr: any = [];
        for(var i=0;i<26;i++){  
            arr.push(String.fromCharCode(65+i));//输出A-Z  26个大写字母  
        }
        this.setState({
            letterArr: arr
        })
    }
    initCity() {
        let {city} = this.props
        let cityArray = city.split(",");
        getCourseCity().then(res => {
            let list = res.data;
            list.map((el) => {
                el.children.map((e) => {
                    if(cityArray.includes(e.city)) {
                        e.selected = true;
                    }
                })
            })
            this.data_letter_sort(list,'province_py')
        })
    }

    data_letter_sort (data, field) {
        let letter_reg = /^[A-Z]$/;
        let list = new Array();
        for (var i = 0; i < data.length; i++) {
            // 添加 # 分组，用来 存放 首字母不能 转为 大写英文的 数据
            // 首字母 转 大写英文
            let letter = (data[i][field]).substr(0, 1).toUpperCase();
            // 创建 字母 分组
            if (!(letter in list)) {
                list[letter] = new Object();
            }
            // 字母 分组 添加 数据
            list[letter] = data[i];
        }
        // 转换 格式 进行 排序；
        var resault = new Array();
        for (var key in list) {
            resault.push({
                letter: key,
                list: list[key]
            });
        }
        resault.sort(function (x, y) {
            return x.letter.charCodeAt(0) - y.letter.charCodeAt(0);
        });
        this.setState({
            cityList: resault
        })
        return resault;
    }

    // 重置城市筛选
    resetCity = () => {
        const {cityList} = this.state
        cityList.map((el) => {
            el.children.map((e) => {
                e.selected = false;
            })
        })
        this.setState({
            cityList: []
        }, () => {
            this.setState({
                cityList
            }) 
        })
    }

    // 更新城市筛选
    updateCity = () => {
        let city: any[] = [];
        let {cityList} = this.state
        cityList.map((el) => {
            el.children.map((e) => {
                if(e.selected) {
                    city.push(e.city)
                }
                
            })
        })
        this.props.onChange(city.toString())
    }
    
    render() {
        const {cityList, letterArr} = this.state;
        console.log(letterArr)
        return (
            <FilterBox 
                onReset={this.resetCity}
                onChange={this.updateCity}
                visible={this.props.visible}
                letterArr={letterArr}
            >
                <div className="filter-city">
                    { cityList !== null && cityList.map((el, index) => (
                        <div className="filter-provice" key={index}>
                            <div className="filter-provice-tt" id={`${el.letter}`}>{el.list.province}</div>
                            <div className="filter-provice-bd">
                                {
                                    el.list.children.map((el, i) => (
                                        <Tag
                                            selected={el.selected}
                                            onChange={(selected) => {el.selected = selected}}
                                            key={i}
                                        >{el.city}</Tag>
                                    ))
                                }
                            </div>
                        </div>
                    ))}
                </div>
            </FilterBox>

        )
    }

}
